import React, { useEffect, useState } from 'react';
import { Helmet } from 'react-helmet';
import { graphql } from 'gatsby';
import Layout from '../components/layout';
import OpeningAnimation from '../components/OpeningAnimation';
import Mainvisual from '../components/Mainvisual';
import Service from '../components/Service';
import About from '../components/About';
import Contact from '../components/Contact';
import Footer from '../components/Footer';
import nodeFetch from 'node-fetch';
import defaultData from '../components/defaultData';

interface IndexPageProps {
  data: {
    site: {
      siteMetadata: {
        lang: string;
        locale: string;
        title: string;
        siteName: string;
        description: string;
        siteUrl: string;
        ogpUrl: string;
        microCmsUrl: string;
        microCmsKey: string;
      };
    };
  };
}

const Index: React.FC<IndexPageProps> = props => {
  const { siteMetadata } = props.data.site;

  const [serviceData, setServiceData] = useState(undefined);
  const [aboutData, setAboutData] = useState(undefined);

  const api = (resource: string): string => {
    return `${siteMetadata.microCmsUrl}${resource}`;
  };

  const fetch = async (dataName: string) => {
    try {
      const url: string = api(dataName);
      const res: any = await nodeFetch(url, {
        headers: {
          'X-API-KEY': siteMetadata.microCmsKey
        }
      });

      if (!res || !res.ok) throw new Error();

      const data: any = await res.json();

      return data.contents;
    } catch (e: any) {
      return defaultData[dataName].contents;
    }
  };

  useEffect(() => {
    const fetchData: any = async () => {
      setServiceData(await fetch('service'));
      setAboutData(await fetch('about'));
    };

    fetchData();
  }, []);

  return (
    <Layout>
      <Helmet>
        <html lang={siteMetadata.lang} />
        <meta name='description' content={siteMetadata.description} />
        <title>{siteMetadata.title}</title>
        <link rel='canonical' href={siteMetadata.siteUrl} />
        <meta property='og:title' content={siteMetadata.title} />
        <meta property='og:type' content='website' />
        <meta property='og:url' content={siteMetadata.siteUrl} />
        <meta property='og:description' content={siteMetadata.description} />
        <meta property='og:locale' content={siteMetadata.locale} />
        <meta property='og:site_name' content={siteMetadata.siteName} />
        <meta property='og:image' content={siteMetadata.ogpUrl} />
        <meta property='og:image:secure_url' content={siteMetadata.ogpUrl} />
        <meta property='og:image:type' content='image/png' />
        <meta property='og:image:width' content='1200' />
        <meta property='og:image:height' content='630' />
        <meta property='og:image:alt' content={siteMetadata.siteName} />
        <meta name='twitter:card' content='summary_large_image' />
        <meta name='twitter:title' content={siteMetadata.title} />
        <meta name='twitter:description' content={siteMetadata.description} />
        <meta name='twitter;image' content={siteMetadata.ogpUrl} />
      </Helmet>
      <Mainvisual />
      <Service data={serviceData} />
      <About data={aboutData} />
      <Contact />
      <Footer />
    </Layout>
  );
};

export const pageQuery = graphql`
  query IndexQuery {
    site {
      siteMetadata {
        lang
        locale
        title
        siteName
        description
        siteUrl
        ogpUrl
        microCmsUrl
        microCmsKey
      }
    }
  }
`;

export default Index;
